import React from 'react'
import Nav from '../layout/nav'
import Teaser from '../layout/teaser'
import SecContainer, { ISecProps } from '../layout/secContainer'
import Footer from '../layout/footer'

import tmpImg from '../../assets/img/UIComponent.png';
import feature1Png from '../../assets/img/feature1.png';
import feature2Png from '../../assets/img/feature2.png';
import feature3Png from '../../assets/img/feature3.png';
import tutorialVideo from '../../assets/img/tutorial.mp4'

export default class Home extends React.Component {
    render() {
        const section1: ISecProps = {
            type: SecContainer.ROW_SEC,
            items: [
                {
                    title: 'Explicit Visual language',
                    content: 'CAST uses a visual language which can explicitly describe the chart animation process.',
                    imgPath: feature1Png
                }, {
                    title: 'No Textual Programming',
                    content: 'CAST avoids textual coding by providing intuitive interactions over the visual language to specify chart animations.',
                    imgPath: feature2Png
                }, {
                    title: 'Efficient Construction',
                    content: 'CAST provides recommandation on keyframe construction and sequencing in order to improve the efficiency and rationality.',
                    imgPath: feature3Png
                }
            ]
        }
        const section2: ISecProps = {
            type: SecContainer.COL_SEC,
            title: 'How It Works',
            content: 'This video shows how CAST works in action with 2 examples scenarios.',
            video: [tutorialVideo]
        }
        return (
            <div>
                <Nav activeTab='home' />
                <div className='container'>
                    <Teaser />
                    <SecContainer type={section1.type} items={section1.items} />
                    <SecContainer type={section2.type} title={section2.title} content={section2.content} video={section2.video} />
                </div>
                <Footer />
            </div>
        )
    }
}