import '../../assets/style/tutorial.scss'

import React from 'react'
import Nav from '../layout/nav'
import Footer from '../layout/footer'
import SideMenu, { ISideItemProps } from '../layout/sideMenu'
import ContentBlock, { IContentBlockProps } from '../layout/content-block'
import FigWithCap, { IFigWithCapProps } from '../layout/figWithCap'
import FigLayout, { IFigLayoutProps } from '../layout/figLayout'

import kfIllusGif from '../../assets/img/kf_illus.gif'
import kfIllusPng from '../../assets/img/kf_illus.png'
import kfgIllusPng from '../../assets/img/kfGroup.png'
import symbolPng from '../../assets/img/icons.png'
import kfTimingPng from '../../assets/img/kf_relation.png'
import kfgTimingPng from '../../assets/img/kfgroup_relation.png'
import aniunitTimingPng from '../../assets/img/kf_align.png'
import uiPng from '../../assets/img/UIComponent.png'
import loadPng from '../../assets/img/nav.png'
import selectToolPng from '../../assets/img/selection.png'
import selSugPng from '../../assets/img/suggestion.png'
import seqPng from '../../assets/img/kf_suggestion.png'
import bindKfTimingPng from '../../assets/img/bind_kf_timing.png'
import adjustKfTimingPng from '../../assets/img/adjust_timing.png'
import bindKfgTimingPng from '../../assets/img/bind_kfg_timing.png'

export default class Tutorial extends React.Component {
    render() {
        const overViewTitle: string = 'overview';
        const overViewLink: string = 'overviewLink';
        const visLangTitle: string = 'visual language';
        const visLangLink: string = 'visLangLink';
        const kfTitle: string = 'keyframe';
        const kfLink: string = 'kfLink';
        const kfgTitle: string = 'keyframe group';
        const kfgLink: string = 'kfgLink';
        const symbolTitle: string = 'symbolic representation';
        const symbolLink: string = 'symbolLink';
        const rTimingTitle: string = 'relative timing';
        const rTimingLink: string = 'timingLink';
        const kfTimingTitle: string = 'Relative Timing Between Keyframes';
        const kfTimingLink: string = '';
        const kfgTimingTitle: string = 'Relative Timing Between Keyframe groups';
        const kfgTimingLink: string = '';
        const aniunitTimingTitle: string = 'Relative Timing Between Aniunits';
        const aniunitTimingLink: string = '';
        const uiTitle: string = 'UI components';
        const uiLink: string = 'uiLink';
        const authorAniTitle: string = 'authoring animations';
        const authorAniLink: string = 'authorLink';
        const importTitle: string = 'importing';
        const importLink: string = 'importLink';
        const chartPrepTitle: string = 'Chart Preparation';
        const chartPrepLink: string = '';
        const loadTitle: string = 'Loading';
        const loadLink: string = '';
        const selectTitle: string = 'selecting';
        const selectLink: string = 'selectLink';
        const selectToolTitle: string = 'Selection Tools';
        const selectToolLink: string = '';
        const selSugTitle: string = 'Suggestion Mode';
        const selSugLink: string = '';
        const seqTitle: string = 'sequencing';
        const seqLink: string = 'sequenceLink';
        const aniTitle: string = 'animating';
        const aniLink: string = 'aniLink';
        const timingToKfTitle: string = 'Specifying Timing to Keyframes';
        const timingToKfLink: string = '';
        const timingToKfgTitle: string = 'Specifying Timing to Keyframe Groups';
        const timingToKfgLink: string = '';

        const menuContent: (ISideItemProps[] | ISideItemProps)[] = [
            {
                content: overViewTitle,
                link: overViewLink
            }, {
                content: visLangTitle,
                link: visLangLink
            }, [
                {
                    content: kfTitle,
                    link: kfLink
                }, {
                    content: kfgTitle,
                    link: kfgLink
                }, {
                    content: symbolTitle,
                    link: symbolLink
                }, {
                    content: rTimingTitle,
                    link: rTimingLink
                }
            ], {
                content: uiTitle,
                link: uiLink
            }, {
                content: authorAniTitle,
                link: authorAniLink
            }, [
                {
                    content: importTitle,
                    link: importLink
                }, {
                    content: selectTitle,
                    link: selectLink
                }, {
                    content: seqTitle,
                    link: seqLink
                }, {
                    content: aniTitle,
                    link: aniLink
                }
            ]
        ]

        const overviewBlock: IContentBlockProps = {
            title: overViewTitle,
            level: 0,
            link: overViewLink,
            content: ['CAST uses a series of visual representations to describe and generate expressive chart animations.']
        }
        const visLangBlock: IContentBlockProps = {
            title: visLangTitle,
            level: 0,
            link: visLangLink,
            content: ['According to Canis grammar, animations of a chart are composed of a series of aniunits. Each aniunit describes the animation of the selected marks by partitioning them into mark untis then bind each mark unit with timing and animation effects.Thus marks in the same unit should animate together and share the same animation properties(duration, delay, effect, easing)']
        }
        const kfBlock: IContentBlockProps = {
            title: kfTitle,
            level: 1,
            link: kfLink,
            content: ['Chart animations can be represented with a keyframe sequence. For each mark unit, CAST selects the last frame of its animation as the keyframe.As shown in the following example, the animation of the facet dot plots can be represented with a series of frames, and one keyframe for each cell is extracted by CAST to describe this animation.']
        }
        const kfIllusBlock: IFigWithCapProps = {
            img: kfIllusGif,
            cap: [
                {
                    para: 'Chart',
                    content: 'Each dot represents one sample. All dots are grouped by Odor along X axis and grouped by Surface along Y axis.Meanwhile Color encodes whether the sample is Edible.'
                }, {
                    para: 'Animation',
                    content: 'For all dots, animate row by row, and within each row, animate by cell.'
                }
            ]
        }
        const kfIllusBlock2: IFigLayoutProps = {
            img: kfIllusPng
        }
        const kfgBlock: IContentBlockProps = {
            title: kfgTitle,
            level: 1,
            link: kfgLink,
            content: ['The animation is used to convey data patterns. Thus the mark unit is created by gradually grouping all visual marks with data variables.And a hierarchical structure will be generated from this grouping procedure.Taking the previous animation as an example, all the dots are first grouped by Surface, then by Odor.CAST uses frames to represent this grouping hierarchy, and the grey scale of the background color of the frame indicates the depth level of this group.']
        }
        const kfgIllusBlock: IFigLayoutProps = {
            img: kfgIllusPng
        }
        const symbolBlock: IContentBlockProps = {
            title: symbolTitle,
            level: 1,
            link: symbolLink,
            content: [
                'CAST uses the following properties to describe the animation of each mark unit:',
                {
                    items: [
                        {
                            para: 'Duration',
                            content: 'The duration time of the animation. Represented by a blue span with an arrow in the center, the width of the span indicates the length of duration time.'
                        }, {
                            para: 'Delay',
                            content: 'The delay time of the animation of this mark unit. Represneted by an orange span with an arrow in the center, the width indicates the length of delay time.'
                        }, {
                            para: 'Effect',
                            content: 'By which kind of effect this mark unit is animated. Each kind of effect has its corresponding icon.'
                        }, {
                            para: 'Easing',
                            content: 'What easing function this mark unit uses to animate. Each easing function has its corresponding icon.'
                        }
                    ]
                },
                'Note: keyframes within the same group have the same animation properties, thus the effect and easing are shown on group level.'
            ]
        }
        const symbolBlock2: IFigLayoutProps = {
            img: symbolPng
        }
        const rTimingBlock: IContentBlockProps = {
            title: rTimingTitle,
            level: 1,
            link: rTimingLink,
            content: ['Having the visual representation for each mark unit (keyframe) is not enough to represent the animation of the whole chart.We still need to show the relative timing relationships between keyframes and between keyframe groups.']
        }
        const kfTimingBlock: IContentBlockProps = {
            title: kfTimingTitle,
            level: 2,
            link: kfTimingLink,
            content: [
                'Between two keyframes, there could be the following four kinds of relative timing relations: ',
                {
                    items: [
                        {
                            para: 'Start with previous',
                            content: 'The second keyframe starts at the same time with the first one.'
                        }, {
                            para: 'Start with previous + delay',
                            content: 'The second keyframe starts after the "delay" time when the first one started.'
                        }, {
                            para: 'Start after previous',
                            content: 'The second keyframe starts right after the first one ends.'
                        }, {
                            para: 'Start after previous + delay',
                            content: 'The second keyframe starts after the "delay" time when the first one ended.'
                        }
                    ]
                }
            ]
        }
        const kfTimingBlock2: IFigLayoutProps = {
            img: kfTimingPng
        }
        const kfgTimingBlock: IContentBlockProps = {
            title: kfgTimingTitle,
            level: 2,
            link: kfgTimingLink,
            content: ['Similar to keyframes, there are also four kinds of relative timing relations between keyframe groups: start with previous, start with previous + delay, start after previous, and start after previous + delay.']
        }
        const kfgTimingBlock2: IFigLayoutProps = {
            img: kfgTimingPng
        }
        const aniunitTimingBlock: IContentBlockProps = {
            title: aniunitTimingTitle,
            level: 2,
            link: aniunitTimingLink,
            content: ['Each aniunit decribes the animation of a series of selected marks. Thus it is represented as a keyframe group as well.The previous example is to align two groups on group level(one group start with/after another). However the relative timing relation between keyframe groups representing aniunits have one more alignment type.Which is to align keyframes within those groups.In order to meet the demand of animating the marks with different shapes but encoded with the same data(e.g. line charts with nodes and labels).As shown in the following example, when one group start after another group and they are aligned on keyframe level, there are two kinds of representations, when they are separated, you can specify animation propeties for each group separatly while they share the same animation properties when they are merged.']
        }
        const aniunitTimingBlock2: IFigLayoutProps = {
            img: aniunitTimingPng
        }
        const uiBlock: IContentBlockProps = {
            title: uiTitle,
            level: 0,
            link: uiLink,
            content: [
                'The user interface of CAST consists of the following components: ',
                {
                    type: 'upper-alpha',
                    items: [
                        {
                            para: 'Data Panel',
                            content: 'Showing the dataset used to generate the chart.'
                        }, {
                            para: 'Chart Panel',
                            content: 'The input chart, which is also for mark selection.'
                        }, {
                            para: 'Animation Specification Panel',
                            content: 'Illustrating and authoring how the chart is animated using static visual representations.'
                        }, {
                            para: 'Animation Preview Panel',
                            content: 'Preview the generated animation.'
                        }
                    ]
                }
            ]
        }
        const uiBlock2: IFigLayoutProps = {
            img: uiPng
        }
        const authorAniBlock: IContentBlockProps = {
            title: authorAniTitle,
            level: 0,
            link: authorAniLink,
            content: [
                'CAST uses the following four steps to author chart animations: ',
                {
                    items: [
                        {
                            para: 'Step 1 (Importing)',
                            content: 'Create chart with Charticulator, Vega, or D3, then using our online or your own customized toolkit to generate the DSVG input.'
                        }, {
                            para: 'Step 2 (Selecting)',
                            content: 'Select the smallest mark unit (marks in 1st keyframe) in animation.'
                        }, {
                            para: 'Step 3 (Sequencing)',
                            content: 'Based on the selected mark unit and marks in the chart, select the desired animation sequence.'
                        }, {
                            para: 'Step 4 (Animating)',
                            content: 'Specify timing (duration, delay) and other animation properties(effect, easing) to each mark unit.'
                        }
                    ]
                },
                'Note that step 2 to step 4 can be repeated to create multiple aniunits in order to specify different animations to different marks.'
            ]
        }
        const importBlock: IContentBlockProps = {
            title: importTitle,
            level: 1,
            link: importLink,
            content: ['']
        }
        const chartPrepBlock: IContentBlockProps = {
            title: chartPrepTitle,
            level: 2,
            link: chartPrepLink,
            content: [
                'Our online DSVG generator</a> which can generate DSVG with charts authored from the following tools:',
                {
                    items: [
                        {
                            para: '',
                            content: 'Charticulator (.chart/.tmplt): data file in csv format is also needed when using the templete file as input. '
                        }, {
                            para: '',
                            content: 'Vega (.json)'
                        }, {
                            para: '',
                            content: 'D3 (.svg)'
                        }
                    ]
                },
                'After importing charts, you can edit the class names in the editor panel, or just hit the download button to download the DSVG file.'
            ]
        }
        const loadBlock: IContentBlockProps = {
            title: loadTitle,
            level: 2,
            link: loadLink,
            content: [
                'Click "Open" on the navigation bar to :',
                {
                    items: [
                        {
                            para: '',
                            content: 'Load Charts: load one or more DSVG files you just prepared.'
                        }, {
                            para: '',
                            content: 'Load Local Project: load the project you saved on your disk.'
                        }, {
                            para: '',
                            content: 'Load Example Project: load one example in the example list.'
                        }
                    ]
                }
            ]
        }
        const loadBlock2: IFigLayoutProps = {
            img: loadPng
        }
        const selectBlock: IContentBlockProps = {
            title: selectTitle,
            level: 1,
            link: selectLink,
            content: ['Mark selection is to decide which marks to animate first (which also decides the scale of the mark unit in one aniunit, for example to animate by row)']
        }
        const selectToolBlock: IFigLayoutProps = {
            img: selectToolPng
        }
        const selectToolBlock2: IContentBlockProps = {
            title: selectToolTitle,
            level: 2,
            link: selectToolLink,
            content: [
                'As shown in the figure. There are three ways to select marks in CAST: ',
                {
                    para: 'Selection Tool',
                    content: ''
                }, {
                    type: 'decimal',
                    items: [
                        {
                            para: '',
                            content: 'Select the selection tool from the tool box on the title of the chart panel.'
                        }, {
                            para: '',
                            content: 'By clicking the mark or dragging a rectangular selection on the chart, one or more marks inside the selection border are being selected.The marks unselected will be translucent while the selected ones are shown inside a bounding box with blue borders on mouse up, and the data records corresponding to the selected marks in the Dataset panel will also be highlighted.'
                        }, {
                            para: '',
                            content: 'To select more, click or drag another rectangular selection on the unselected marks.'
                        }, {
                            para: '',
                            content: 'To deselect marks, click or drag rectangular selection on the selected marks.'
                        }
                    ]
                }, {
                    para: 'Lasso Tool',
                    content: ''
                }, {
                    type: 'decimal',
                    items: [
                        {
                            para: '',
                            content: 'Select the lasso tool from the tool box on the title of the chart panel.'
                        }, {
                            para: '',
                            content: 'Position the cursor somewhere outside the marks.'
                        }, {
                            para: '',
                            content: 'Trace around the marks you want to select and capture them to retain in your selection.'
                        }, {
                            para: '',
                            content: 'To select more, just repeat step 2 and step 3 on the unselected marks.'
                        }, {
                            para: '',
                            content: 'To deselect marks, do lasso selection on the selected marks.'
                        }
                    ]
                }, {
                    para: 'Data Table',
                    content: ''
                }, {
                    type: 'decimal',
                    items: [
                        {
                            para: '',
                            content: 'On the Dataset Panel, click the column title to sort the column by this data attribute. If clicking on a non- sorted column, all data records will be sorted in asscending order according to this data attribute; if the column is already sorted, then it will switch to the opposite order(asscending to descending, descending to asscending).'
                        }, {
                            para: '',
                            content: 'Click on the row to select one data record. To select a range, select a row, then with the left mouse button pressed, drag over to other rows.Or use "Shift + click" to select non- adjacent rows.Meanwhile the corresponding marks in the chart will be selected.'
                        }, {
                            para: '',
                            content: 'To deselect marks, click or "click + drag over" on the selected rows.'
                        }
                    ]
                }
            ]
        }
        const selSugBlock: IContentBlockProps = {
            title: selSugTitle,
            level: 2,
            link: selSugLink,
            content: ['As shown in the figure in Selection Tools, there is one more button in the toolbox of chart panel. It is used to activate / deactivate the suggestion mode of the selection.When the suggestion mode is activated, the system will automatically select marks from the unselected ones with the same values of the same categorical attributes as those user- selected marks.Here is an example shown below:']
        }
        const selSugBlock2: IFigLayoutProps = {
            img: selSugPng
        }
        const seqBlock: IContentBlockProps = {
            title: seqTitle,
            level: 1,
            link: seqLink,
            content: [
                'There are small plus buttons before keyframe groups who have marks that can be further partitioned into smaller mark units in order to animate them in a more detailed animation sequence.Once the marks are selected in the Chart Panel, drag the selected marks to the Keyframe Panel, and those plus buttons before the keyframe groups who can be further partitioned with the selected marks as one mark unit will be highlighted.Drop the selected marks on one plue button and you will start to create new animation sequence of the marks of the corresponding keyframe group.',
                'An example is shown in the figure below.'
            ]
        }
        const seqBlock2: IFigLayoutProps = {
            img: seqPng
        }
        const aniBlock: IContentBlockProps = {
            title: aniTitle,
            level: 1,
            link: aniLink,
            content: [
                'This step is to bind animation properties to to each keyframe group and keyframe. Including timing properties(duration and delay) and effect properties(effect type and easing)'
            ]
        }
        const timingToKfBlock: IContentBlockProps = {
            title: timingToKfTitle,
            level: 2,
            link: timingToKfLink,
            content: [
                'Each keyframe has a duration bar indicating the duration of the animation of its corrsponding mark unit.Drag the bar on the bottom of the keyframe to add / remove delay or adjust relative timing (start with/after previous). Note that keyframes in the same group should share the same animation property values.Thus to update one keyframe will update all keyframe within the same group.',
                'Drag the right border of the delay/duration bar or enter the exact number in the popup when hovering mouse on the delay / duration bar to adjust the length of the duration or delay.'
            ]
        }
        const timingToKfBlock2: IFigLayoutProps = {
            img: bindKfTimingPng
        }
        const timingToKfBlock3: IFigLayoutProps = {
            img: adjustKfTimingPng
        }
        const timingToKfgBlock: IContentBlockProps = {
            title: timingToKfgTitle,
            level: 2,
            link: timingToKfgLink,
            content: [
                'The duration of each keyframe group is determined by the total duration of the keyframes within it. Thus we can only specify delay and relative timing bewteen keyframe groups.'
            ]
        }
        const timingToKfgBlock2: IFigLayoutProps = {
            img: bindKfgTimingPng
        }

        return (
            <div>
                <Nav activeTab='tutorial' />
                <div className='container'>
                    <div className='tutorial-wrapper'>
                        <SideMenu menuContent={menuContent} />
                        <div className='content-wrapper'>
                            <ContentBlock title={overviewBlock.title} level={overviewBlock.level} link={overviewBlock.link} content={overviewBlock.content} />
                            <ContentBlock title={visLangBlock.title} level={visLangBlock.level} link={visLangBlock.link} content={visLangBlock.content} />
                            <ContentBlock title={kfBlock.title} level={kfBlock.level} link={kfBlock.link} content={kfBlock.content} />
                            <FigWithCap img={kfIllusBlock.img} cap={kfIllusBlock.cap} />
                            <FigLayout img={kfIllusBlock2.img} />
                            <ContentBlock title={kfgBlock.title} level={kfgBlock.level} link={kfgBlock.link} content={kfgBlock.content} />
                            <FigLayout img={kfgIllusBlock.img} />
                            <ContentBlock title={symbolBlock.title} level={symbolBlock.level} link={symbolBlock.link} content={symbolBlock.content} />
                            <FigLayout img={symbolBlock2.img} />
                            <ContentBlock title={rTimingBlock.title} level={rTimingBlock.level} link={rTimingBlock.link} content={rTimingBlock.content} />
                            <ContentBlock title={kfTimingBlock.title} level={kfTimingBlock.level} link={kfTimingBlock.link} content={kfTimingBlock.content} />
                            <FigLayout img={kfTimingBlock2.img} />
                            <ContentBlock title={kfgTimingBlock.title} level={kfgTimingBlock.level} link={kfgTimingBlock.link} content={kfgTimingBlock.content} />
                            <FigLayout img={kfgTimingBlock2.img} />
                            <ContentBlock title={aniunitTimingBlock.title} level={aniunitTimingBlock.level} link={aniunitTimingBlock.link} content={aniunitTimingBlock.content} />
                            <FigLayout img={aniunitTimingBlock2.img} />
                            <ContentBlock title={uiBlock.title} level={uiBlock.level} link={uiBlock.link} content={uiBlock.content} />
                            <FigLayout img={uiBlock2.img} />
                            <ContentBlock title={authorAniBlock.title} level={authorAniBlock.level} link={authorAniBlock.link} content={authorAniBlock.content} />
                            <ContentBlock title={importBlock.title} level={importBlock.level} link={importBlock.link} content={importBlock.content} />
                            <ContentBlock title={chartPrepBlock.title} level={chartPrepBlock.level} link={chartPrepBlock.link} content={chartPrepBlock.content} />
                            <ContentBlock title={loadBlock.title} level={loadBlock.level} link={loadBlock.link} content={loadBlock.content} />
                            <FigLayout img={loadBlock2.img} />
                            <ContentBlock title={selectBlock.title} level={selectBlock.level} link={selectBlock.link} content={selectBlock.content} />
                            <FigLayout img={selectToolBlock.img} />
                            <ContentBlock title={selectToolBlock2.title} level={selectToolBlock2.level} link={selectToolBlock2.link} content={selectToolBlock2.content} />
                            <ContentBlock title={selSugBlock.title} level={selSugBlock.level} link={selSugBlock.link} content={selSugBlock.content} />
                            <FigLayout img={selSugBlock2.img} />
                            <ContentBlock title={seqBlock.title} level={seqBlock.level} link={seqBlock.link} content={seqBlock.content} />
                            <FigLayout img={seqBlock2.img} />
                            <ContentBlock title={aniBlock.title} level={aniBlock.level} link={aniBlock.link} content={aniBlock.content} />
                            <ContentBlock title={timingToKfBlock.title} level={timingToKfBlock.level} link={timingToKfBlock.link} content={timingToKfBlock.content} />
                            <FigLayout img={timingToKfBlock2.img} />
                            <FigLayout img={timingToKfBlock3.img} />
                            <ContentBlock title={timingToKfgBlock.title} level={timingToKfgBlock.level} link={timingToKfgBlock.link} content={timingToKfgBlock.content} />
                            <FigLayout img={timingToKfgBlock2.img} />
                            <a href="test" >Michael</a>
                        </div>
                    </div>
                </div>
                <Footer />
            </div>
        )
    }
}