import React from 'react'

import Nav from '../layout/nav'
import Footer from '../layout/footer'
import PeopleList from '../layout/peopleList'

export default class About extends React.Component {
    render() {
        return (
            <div>
                <Nav activeTab='about' />
                <div className='container'>
                    <PeopleList peopleInfos={[
                        {
                            imgCls: 'tge',
                            title: 'Tong Ge',
                            content: 'Shandong University'
                        }, {
                            imgCls: 'bl',
                            title: 'Bongshin Lee',
                            content: 'Microsoft Research'
                        }, {
                            imgCls: 'bqc',
                            title: 'Baoquan Chen',
                            content: 'Peking University'
                        }, {
                            imgCls: 'yhw',
                            title: 'Yunhai Wang',
                            content: 'Shandong University'
                        }
                    ]} />
                </div>
                <Footer />
            </div>
        )
    }
}