import '../../assets/style/fig-txt-card.scss'
import React from 'react'

export interface IFigTxtCardProps {
    imgCls: string
    title: string
    content: string
}

export class FigTxtCard extends React.Component<IFigTxtCardProps> {
    render() {
        return (
            <div className='card-item'>
                <div className={`card-img ${this.props.imgCls}`}></div>
                <h4>{this.props.title}</h4>
                <p>{this.props.content}</p>
            </div>
        );
    }
}