import '../../assets/style/fig-layout.scss'
import React from 'react'

export interface IFigLayoutProps {
    img: string
}

export default class FigLayout extends React.Component<IFigLayoutProps> {
    render() {
        return (
            <div className='single-col-img-container'>
                <img alt='' src={this.props.img} />
            </div>
        )
    }
}