import React from 'react'

export interface IParaProps {
    para: string
    content: string
}

interface IListProps {
    type?: string
    items: IParaProps[]
}

function isPara(obj: any): obj is IParaProps {
    return 'para' in obj;
}

// function isList(obj: any): obj is IListProps {
//     return 'items' in obj;
// }

export interface IContentBlockProps {
    title: string
    level: number
    link: string
    content: (string | IParaProps | IListProps)[]
}

export default class ContentBlock extends React.Component<IContentBlockProps> {
    render() {
        let titleComp: JSX.Element;
        switch (this.props.level) {
            case 0:
                titleComp = <h1 id={this.props.link}>{this.props.title}</h1>
                break;
            case 1:
                titleComp = <h2 id={this.props.link}>{this.props.title}</h2>
                break;
            case 2:
                titleComp = <h3 id={this.props.link}>{this.props.title}</h3>
                break;
            default:
                titleComp = <h1 id={this.props.link}>{this.props.title}</h1>
                break;
        }

        const blockContent: JSX.Element[] = this.props.content.map((tmpContent: string | IParaProps | IListProps, idx: number) => {
            if (typeof tmpContent === 'string') {
                return <p key={idx}>{tmpContent}</p>
            } else if (isPara(tmpContent)) {
                return <b key={idx}>{tmpContent.para}</b>
            } else {//generate ul
                const items: JSX.Element[] = tmpContent.items.map((li: IParaProps, itemIdx: number) => <li key={itemIdx}><b>{li.para}</b>{li.para.length > 0 ? ':' : ''} {li.content}</li>)
                if (typeof tmpContent.type !== 'undefined') {
                    return <ol key={idx} style={{ listStyleType: tmpContent.type }}>{items}</ol>
                } else {
                    return <ul key={idx}>{items}</ul>
                }
            }
        })
        return (
            <div>
                {titleComp}
                {blockContent}
            </div>
        )
    }
}