import '../../assets/style/teaser.scss'
import React from 'react'

export default class Teaser extends React.Component {
    render() {
        // const title: string = 'CAST - Canis Studio XXX';
        const title: JSX.Element = <h2>CAST - Canis Studio</h2>;
        const teaserCap: string = `CAST provides an easy access to authoring expressive chart animations especially for people
                                    who lacks programming skills. It represents the animation with the means of an intuitive visual language. 
                                    And by interactively editing the visual elements, the designer can alter the animation properties. 
                                    Under the keyframe-based authoring paradigm, CAST enables the designer to create animations in three steps: 
                                    keyframe construction, keyframe sequencing and keyframe properties specification. Meanwhile, in order to 
                                    improve the efficiency and rationality of the result animations, CAST also proposed suggestion strategies in
                                    keyframe construction and sequencing.`
        const toSystem = () => {
            window.location.href = 'https://chartanimation.github.io/cast/system/';
        }
        const toCanis = () => {
            window.location.href = 'https://chartanimation.github.io/canis/';
        }
        return (
            <div className='teaser'>
                <div className='teaser-text-container'>
                    {/* <h2>{title}</h2> */}
                    {title}
                    <p>{teaserCap}</p>
                    <button className='launchBtn' onClick={toSystem}>CREATE YOUR ANIMATION</button>
                    <button className='launchBtn' onClick={toCanis}>VISIT CANIS</button>
                </div>
            </div>
        );
    }
}