import '../../assets/style/fig-cap.scss'
import React from 'react'
import { IParaProps } from './content-block'

export interface IFigWithCapProps {
    img: string
    cap: IParaProps[]
}

export default class FigWithCap extends React.Component<IFigWithCapProps> {
    render() {
        const capContent: JSX.Element[] = this.props.cap.map((tmpPara: IParaProps, idx: number) => {
            return <p key={idx}><b>{tmpPara.para}</b>: {tmpPara.content}</p>
        })

        return (
            <div className='fig-cap-container'>
                <div className='figure'>
                    <img src={this.props.img} />
                </div>
                <div className='cap-text'>
                    {capContent}
                </div>
            </div>
        )
    }
}