import '../../assets/style/gallery-detail.scss'
import React from 'react'
import Nav from '../layout/nav'
import Footer from '../layout/footer'

import ReactPlayer from 'react-player'
import { IAniTxtCardProps } from '../layout/aniTxtCard'

interface IGalleryDetailProps {
    title: string
    chartImg: string
    aniVideo: string
    description: string[]
    teachVideo: string
}

export default class GalleryDetail extends React.Component<{ match: any }, IGalleryDetailProps> {
    state = {
        title: '',
        chartImg: '',
        aniVideo: '',
        description: ['', ''],
        teachVideo: ''
    }
    componentDidMount() {
        const itemProps: IAniTxtCardProps = JSON.parse(localStorage.getItem("itemProps") + '');
        this.setState({
            title: itemProps.title,
            chartImg: itemProps.image,
            aniVideo: itemProps.aniVideo,
            teachVideo: itemProps.teachVideo,
            description: itemProps.description
        })
    }
    render() {
        return (
            <div>
                <Nav activeTab='gallery' />
                <div className='container'>
                    <div className='inner-container'>
                        <h3>{this.state.title}</h3>
                        <div className='description-contianer'>
                            <div className='img-container'>
                                <img src={this.state.chartImg} />
                            </div>
                            <div className='img-container'>
                                <ReactPlayer url={this.state.aniVideo} controls={true} width={'auto'} height={'100%'} playing={true} />
                            </div>
                            <div className='txt-container'>
                                <p><b>Chart</b>: {this.state.description[0]}</p>
                                <p><b>Animation</b>: {this.state.description[1]}</p>
                            </div>
                        </div>
                        <br /><br /><br />
                        <h3>How the animation is created: </h3>
                        <ReactPlayer url={this.state.teachVideo} controls={true} width={'100%'} height={'auto'} />
                    </div>
                </div>
                <Footer />
            </div>
        )
    }
}