import '../../assets/style/people-list.scss'
import React from 'react'
import { FigTxtCard, IFigTxtCardProps } from './figTxtCard';

export default class PeopleList extends React.Component<{ peopleInfos: IFigTxtCardProps[] }> {
    render() {
        const pList: JSX.Element[] = this.props.peopleInfos.map((pProps: IFigTxtCardProps) => <FigTxtCard imgCls={pProps.imgCls} title={pProps.title} content={pProps.content} />)
        return (
            <div className='people-wrapper'>
                <h2>People</h2>
                {pList}
            </div>
        );
    }
}