import '../../assets/style/nav.scss'
import logoPng from '../../assets/img/logo.png'
import React from 'react'

interface INavProps {
    activeTab: string
}
interface IState { }

export default class Nav extends React.Component<INavProps, IState> {
    render() {
        let menuTabs: string[] = ['home', 'gallery', 'tutorial', 'about'];
        const menuList = menuTabs.map((tab: string, idx: number) => {
            if (idx === 0) {
                return (
                    <li key={idx} className={this.props.activeTab === tab ? 'active-item' : ''}>
                        <a href='#/'>{tab}</a>
                    </li>
                );
            } else {
                return (
                    <li key={idx} className={this.props.activeTab === tab ? 'active-item' : ''}>
                        <a href={`#/${tab}`}>{tab}</a>
                    </li>
                )
            }
        })
        function toSystem() {
            window.location.href = 'https://chartanimation.github.io/cast/system/';
        }
        return (
            <header>
                <img className='logo' src={logoPng} alt='' />
                <div className='menu-container'>
                    <button className='launchBtn'
                        onClick={toSystem}>Launch</button>
                    <ul className='menu'>
                        {menuList}
                    </ul>

                </div>
            </header>
        );
    }
}