import '../../assets/style/gallery.scss'

import React from 'react'
import Nav from '../layout/nav'
import AniTxtCard, { IAniTxtCardProps } from '../layout/aniTxtCard'
import Footer from '../layout/footer'

import pieLottie from '../../assets/img/gallery/pie.json'
import piePng from '../../assets/img/gallery/pie.png'
import pieVideo from '../../assets/img/gallery/pie.mp4'
import pieTVideo from '../../assets/img/gallery/pie_t.mp4'
import lineLottie from '../../assets/img/gallery/line.json'
import linePng from '../../assets/img/gallery/line.png'
import lineVideo from '../../assets/img/gallery/line.mp4'
import lineTVideo from '../../assets/img/gallery/line_t.mp4'
import osLottie from '../../assets/img/gallery/os.json'
import osPng from '../../assets/img/gallery/os.png'
import osVideo from '../../assets/img/gallery/os.mp4'
import osTVideo from '../../assets/img/gallery/os_t.mp4'
import co2Lottie from '../../assets/img/gallery/co2.json'
import co2Png from '../../assets/img/gallery/co2.png'
import co2Video from '../../assets/img/gallery/co2.mp4'
import co2TVideo from '../../assets/img/gallery/co2_t.mp4'
import polioLottie from '../../assets/img/gallery/polio.json'
import polioPng from '../../assets/img/gallery/polio.png'
import polioVideo from '../../assets/img/gallery/polio.mp4'
import polioTVideo from '../../assets/img/gallery/polio_t.mp4'
import mushroomLottie from '../../assets/img/gallery/mushroom.json'
import mushroomPng from '../../assets/img/gallery/mushroom.png'
import mushroomVideo from '../../assets/img/gallery/mushroom.mp4'
import mushroomTVideo from '../../assets/img/gallery/mushroom_t.mp4'
import drivingLottie from '../../assets/img/gallery/driving.json'
import drivingPng from '../../assets/img/gallery/driving.png'
import drivingVideo from '../../assets/img/gallery/driving.mp4'
import drivingTVideo from '../../assets/img/gallery/driving_t.mp4'
import flareLottie from '../../assets/img/gallery/flare.json'
import flarePng from '../../assets/img/gallery/flare.png'
import flareVideo from '../../assets/img/gallery/flare.mp4'
import flareTVideo from '../../assets/img/gallery/flare_t.mp4'
import populationLottie from '../../assets/img/gallery/population.json'
import populationPng from '../../assets/img/gallery/population.png'
import populationVideo from '../../assets/img/gallery/population.mp4'
import populationTVideo from '../../assets/img/gallery/population_t.mp4'

export default class Gallery extends React.Component {
    render() {
        const sendData = (e: any, itemProps: IAniTxtCardProps) => {
            localStorage.setItem("itemProps", JSON.stringify(itemProps));
            window.location.href = '#/gallery-detail';
        }
        const itemsProps: IAniTxtCardProps[] = [
            {
                image: piePng,
                lottie: pieLottie,
                aniVideo: pieVideo,
                teachVideo: pieTVideo,
                title: 'Casuses of Mortality over Time',
                description: [
                    'Florence Nightingale’s rose chart depicting the causes of mortality in the army in the east.',
                    'The animation starts with animating all axis, legend and title first, then the widgets wheel in one after another, class after class.'
                ]
            }, {
                image: linePng,
                lottie: lineLottie,
                aniVideo: lineVideo,
                teachVideo: lineTVideo,
                title: 'Doughnut Purchases (1996 - 2014)',
                description: [
                    'A line chart describing the trend of doughnut purchases from 1996 to 2014.',
                    'Axis and title first fade out. Then the line starts to grow, meanwhile the dot fades out when the line reaches it, and the corresponding label wipes in at the same time.'
                ]
            }, {
                image: osPng,
                lottie: osLottie,
                aniVideo: osVideo,
                teachVideo: osTVideo,
                title: 'Mobile Operating System Market Share',
                description: [
                    'This ribbon chart shows the market share of mobile operating systems from 2009 to 2016.',
                    'The axis and title fade in first. Then each series of bars linked with bands wipes in one after another.'
                ]
            }, {
                image: co2Png,
                lottie: co2Lottie,
                aniVideo: co2Video,
                teachVideo: co2TVideo,
                title: 'Ranking of Carbon Dioxide Emissions',
                description: [
                    'This multiline chart shows the ranking of CO2 emissions of selected countries from 1992 to 2011.',
                    'The axis and title fade in first, then the lines from all series start to grow from left to right, the corresponding dot fades out when the line reaches its position.'
                ]
            }, {
                image: polioPng,
                lottie: polioLottie,
                aniVideo: polioVideo,
                teachVideo: polioTVideo,
                title: 'Polio Incidence Rates in 1950s',
                description: [
                    'This facet map shows the Polio incidence rates for each American state in 1950s.',
                    'Title and label for each map fade in first, then all dots start to fade in follows the asscending order of their corresponding rate number.'
                ]
            }, {
                image: mushroomPng,
                lottie: mushroomLottie,
                aniVideo: mushroomVideo,
                teachVideo: mushroomTVideo,
                title: 'Mushroom Samples',
                description: [
                    'This facet dot plot displays the charactoristics of mushrooms sampled from the UCI mushroom dataset.',
                    'After the axis, legend, title fade in, dots animate cell by cell from the bottom left corner to the top right corner.'
                ]
            }, {
                image: drivingPng,
                lottie: drivingLottie,
                aniVideo: drivingVideo,
                teachVideo: drivingTVideo,
                title: 'The Price of the Oil',
                description: [
                    'This chart shows the relationship between the driving distance and oil price over time. In this chart, each dot corresponds to a year which is annotated with a text label next to the dot. Dots of consecutive years are connected with links.',
                    'The animation starts with all dots fading in together. Then the link starts growing according to the chronological order and the labels fade in immediately when the link reaches the corresponding dot.'
                ]
            }, {
                image: flarePng,
                lottie: flareLottie,
                aniVideo: flareVideo,
                teachVideo: flareTVideo,
                title: 'Solar Flares',
                description: [
                    'This is a bubble chart with labels depicting a subset of the UCI Solar Flares dataset.',
                    'The bubbles grow in class by class, after the animation of each class of bubbles, their corresponding labels wipe in.'
                ]
            }, {
                image: populationPng,
                lottie: populationLottie,
                aniVideo: populationVideo,
                teachVideo: populationTVideo,
                title: 'World Population Pyramid of 2017',
                description: [
                    'This is the population pyramid of the world in 2017, grouped by age and gender. ',
                    'The axis and title fade in first, then animate bars of both sex by age range. '
                ]
            }
        ]
        const links: JSX.Element[] = itemsProps.map((itemProps: IAniTxtCardProps) => {
            return (
                <a onClick={(e) => { sendData(e, itemProps) }}>
                    <AniTxtCard image={itemProps.image} lottie={itemProps.lottie} title={itemProps.title} description={[]} aniVideo={''} teachVideo={''} />
                </a >
            )
        })

        return (
            <div>
                <Nav activeTab='gallery' />
                <div className='container'>
                    <div className='gallery-list'>
                        {links}
                    </div>
                </div>
                <Footer />
            </div >
        )
    }
}