import '../../assets/style/side-menu.scss'
import React from 'react'

export interface ISideItemProps {
    content: string
    link: string
}

export default class SideMenu extends React.Component<{ menuContent: (ISideItemProps[] | ISideItemProps)[] }> {
    render() {
        const scrollToDiv = (divId: string) => {



        }

        const handleClick = (e: any, divId: string) => {
            let targetTop: number | undefined = document.getElementById(divId)?.offsetTop;
            if (typeof targetTop === 'undefined') {
                targetTop = 0;
            } else {
                targetTop += -100;
            }
            window.scrollTo({
                top: targetTop,
                behavior: "smooth"
            })
        }
        const generateSubMenu = (subMenuItems: (ISideItemProps[] | ISideItemProps)[], level: number): JSX.Element => {
            const items: JSX.Element[] = subMenuItems.map((itemProp: ISideItemProps | ISideItemProps[], idx: number) => {
                if (itemProp instanceof Array) {
                    return (
                        <li key={idx} className='nav-item'>
                            {generateSubMenu(itemProp, 1)}
                        </li>
                    );
                } else {
                    return (
                        <li key={idx} className='nav-item'>
                            <a onClick={(e) => { handleClick(e, itemProp.link) }}>{itemProp.content}</a>
                        </li>
                    );
                }
            })
            return (
                <ul className={level === 0 ? 'sidebar-nav' : 'sub-nav'}>
                    {items}
                </ul>
            );
        }

        return (
            <aside className='sidebar-wrapper'>
                {generateSubMenu(this.props.menuContent, 0)}
            </aside>
        );
    }
}