import '../../assets/style/ani-txt-card.scss'

import React from 'react'
import Lottie from 'react-lottie'

export interface IAniTxtCardProps {
    image: string
    lottie: any
    title: string
    description: string[]
    aniVideo: string
    teachVideo: string
}

interface IAniTxtCardState {
    isPaused: boolean
}

export default class AniTxtCard extends React.Component<IAniTxtCardProps, IAniTxtCardState> {
    constructor(props: any) {
        super(props);
        this.state = { isPaused: true }
    }
    render() {
        const mouseenterItem = () => {
            this.setState({ isPaused: false })
        }
        const mouseleaveItem = () => {
            this.setState({ isPaused: !this.state.isPaused })
        }
        const defaultOptions = {
            loop: true,
            autoplay: false,
            animationData: this.props.lottie,
            rendererSettings: {
                preserveAspectRatio: 'xMidYMid slice'
            }
        };
        return (
            <div className='rect-ani-card' onMouseEnter={mouseenterItem} onMouseLeave={mouseleaveItem}>
                <div className='card-img' style={{ backgroundImage: 'url(' + this.props.image }}>
                    <Lottie options={defaultOptions}
                        height={140}
                        width={210}
                        isPaused={this.state.isPaused} />
                </div>
                <div className='card-text'>
                    <span>{this.props.title}</span>
                </div>
            </div>
        );
    }
}
